import React, { useEffect, useState } from "react";
import { Typography, Box, AppBar, Toolbar } from "@mui/material";
import { commonUrl } from "../config.js";
import CircularProgress from "@mui/material/CircularProgress";
import logoImg from "../assets/cloud_computing.png";
export default function AppBars() {
  const [pageCounter, setPageCounter] = useState(0);
  const [isloaded, setLoaded] = useState(true);
  useEffect(() => {
    if (isloaded) {
      getVisitCount();
      setLoaded(false);
    }
  }, []);

  const getVisitCount = (params) => {
    const options = {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      //body: JSON.stringify({ symbol: params }),
    };
    fetch(`${commonUrl}/visitcount`, options)
      .then((response) => response.json())
      .then((response) => {
        if (response.status === 200) {
          setPageCounter(response.count);
          //console.log(response, "res");
        } else {
          console.log(response, "no data");
        }
      })
      .catch((err) => console.error(err, "error"));
  };
  return (
    <div>
      <Box sx={{ flexGrow: 1 }}>
        <AppBar position="static">
          <Toolbar>
            <Box>
              <img
                alt="Logo"
                src={logoImg}
                style={{ width: 50, paddingLeft: 5, paddingRight: 20 }}
              />
            </Box>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              Invest Booster
            </Typography>
            <Typography variant="h6" component="div">
              Page Visit:{" "}
              {pageCounter === 0 ? (
                <CircularProgress color="inherit" size={15} />
              ) : (
                pageCounter
              )}
            </Typography>
          </Toolbar>
        </AppBar>
      </Box>
    </div>
  );
}
