import React, { useState, useEffect } from "react";
import { useStockProvider } from "../../context/stock_provider";
import { Grid, Paper, Typography } from "@mui/material";
import { commonUrl } from "../../config.js";

export default function Chart() {
  const [value, setValue] = useState();
  const [loading, setLoading] = useState(true);
  const [periodValue, setPeriodValue] = useState("1Y");
  const pierods = ["1d", "1M", "3M", "1Y", "5Y", "all"];
  const { stockSymbol } = useStockProvider();

  const getChart = () => {
    setLoading(true);
    const options = {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ symbol: stockSymbol, period: periodValue }),
    };

    fetch(`${commonUrl}/chart`, options)
      .then((response) => response.json())
      .then((response) => {
        //console.log(response.data);
        var buf = new Uint8Array(response.data);
        var buftobase = arrayBufferToBase642(buf);
        var image = `data:image/png;base64,${buftobase}`;
        setValue(image);
        setLoading(false);
      })
      .catch((err) => console.error(err, "error"));
  };
  function arrayBufferToBase642(buffer) {
    var binary = "";
    var bytes = new Uint8Array(buffer);
    var len = bytes.byteLength;
    for (var i = 0; i < len; i++) {
      binary += String.fromCharCode(bytes[i]);
    }
    return window.btoa(binary);
  }

  useEffect(() => {
    getChart();
  }, [stockSymbol, periodValue]);
  return (
    <div>
      <Grid container spacing={1} justifyContent="center">
        {pierods.map((value) => (
          <Grid item onClick={() => setPeriodValue(value)} key={value}>
            <Paper sx={{ paddingY: "3px", paddingX: "8px" }} elevation={1}>
              <Typography variant="body2" style={{ pointerEvents: "none" }}>
                {value}
              </Typography>
            </Paper>
          </Grid>
        ))}
      </Grid>
      {loading ? (
        <Grid container spacing={1} justifyContent="center" margin={1}>
          <Typography variant="subtitle" style={{ pointerEvents: "none" }}>
            Loading...
          </Typography>
        </Grid>
      ) : (
        <Paper
          onClick={() => {
            window.open(value, "_blank");
          }}
        >
          <img alt="data" src={value} style={{ height: "auto", width: 400 }} />
        </Paper>
      )}
    </div>
  );
}
